@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Chivo+Mono:wght@200&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;1,400&display=swap');


@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components{
  html{
    font-family: 'Poppins', sans-serif;
  }

  .container{
    padding-left: 5px;
    padding-right: 5px;
    margin-left: auto;
    margin-right: auto;
  }

  @media (min-width: 768px) {
    .container{
      width: 750px;
    }
  }
  @media (min-width: 992) {
    .container{
      width: 970px;
    }
  }
  @media (min-width: 1200px) {
    .container{
      width: 1170px;
    }
  }
}